@import url(https://fonts.googleapis.com/css2?family=Courgette&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* Holds CoffeeSelect and CoffeeCard */
.CoffeeStuff {
  display: flex;
  justify-content: center;
  padding: 10px;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

h1 {
  font-family: 'Courgette', cursive;
}
.CoffeeStuff {
  max-width: 1000px;
  margin: 0 auto;
}

/* Card styling */
.CoffeeCard {
  background-color: #fbf2c0;
  border: solid 3px #48392a;
  border-radius: 10px;
  width: 500px;
  padding: 25px;
  display: flex;
  margin-left: 35px;
}

/* Image and description area */
.CoffeeCard .LeftSide {
  width: 100px;
  padding: 10px;
}

/* Placeholder content for the coffee image */
.CoffeeCard .LeftSide .CoffeeImage {
  width: 100px;
  height: 100px;
  background-color: #43281c;
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
}

.CoffeeCard .LeftSide .CoffeeImage img {
  width: 90px;
  height: 90px;
}

/* Description below image */
.CoffeeCard .LeftSide .CoffeeDescription {
  margin-top: 10px; /* adjust as needed */
  color: #43281c;
  font-size: 16px;
  text-align: left;
}

/* Name and info area */
.CoffeeCard .RightSide {
  /* background-color: green; */
  margin-left: 30px;
}

.CoffeeCard .RightSide .CardHeader {
  display: flex;
}

/* Name of coffee on card */
.CoffeeCard .CoffeeName {
  color: #f96f5d;
  font-size: 40px;
}

/* Subtext below coffee name */
.CoffeeCard .CoffeeName .CoffeeSubtext {
  color: #c06e52;
  font-size: 17px;
  display: flex;
}

/* Area with the small and large info boxes */
.CoffeeCard .InfoBoxes {
  display: inline;
}

/* Area with the 4 small boxes */
.CoffeeCard .InfoBoxes .SmallBoxes {
  display: flex;
  margin-top: 10px;
}

/* Common style for all small boxes */
.CoffeeCard .InfoBoxes .SmallBoxes .InfoBox {
  padding: 10px;
  border: solid 2px #43281c;
  background-color: whitesmoke;
  margin: 0 auto;
  max-width: 75px;
  margin-right: 10px;
}

/* Set properties for box 1 */
.CoffeeCard .InfoBoxes .one {
  /* background-color: red; */
}

/* Set properties for box 2 */
.CoffeeCard .InfoBoxes .two {
  /* background-color: green; */
}

/* Set properties for box 3 */
.CoffeeCard .InfoBoxes .three {
  /* background-color: yellow; */
}

/* Set properties for box 4 */
.CoffeeCard .InfoBoxes .four {
  /* background-color: purple; */
}

/* Large info box */
.CoffeeCard .InfoBoxes .LargeBox {
  padding: 10px;
  border: solid 2px #43281c;
  background-color: whitesmoke;
  margin-top: 10px;
  margin-right: 10px;
  text-align: left;
  margin-bottom: 10px;
}

/* Prevent text in large box from affecting layout and size of other elements */
.CoffeeCard .InfoBoxes .LargeBox .LargeBoxText {
  max-width: -webkit-fit-content;
  max-width: -moz-fit-content;
  max-width: fit-content;
}

/* CoffeeSelect */

.CoffeeSelect {
  /*margin-left: calc(-35% - 20px);*/
  margin-left: -700px;
  position: absolute;
}

/* these should be circles */
.CoffeeSelect .CoffeeIcon {
  border: solid 2px black;
  border-radius: 100px;
  margin-top: 30px;
  display: inherit;
  width: 100%;
  padding: 5px;
  cursor: pointer;
}
/* styling when hovered */
.CoffeeSelect .CoffeeIcon:hover {
  border: solid 2px black;
  border-radius: 100px;
  margin-top: 30px;
  display: inherit;
  width: 100%;
  padding: 5px;
  cursor: pointer;
  background-color: #d1cca6;
}
/* styling when clicked */
.CoffeeSelect .CoffeeIcon:active {
  border: solid 2px black;
  border-radius: 100px;
  margin-top: 30px;
  display: inherit;
  width: 100%;
  padding: 5px;
  cursor: pointer;
  background-color: #a39c76;
}

/* animation goes here */
/* slide in */
.Coffee-Slide-enter {
  opacity: 0;
  margin-top: 400px;
}

.Coffee-Slide-enter-active {
  opacity: 1;
  margin-top: 0px;
  transition: 800ms;
}
/* slide out */
.Coffee-Slide-exit {
  opacity: 1;
}

.Coffee-Slide-exit-active {
  opacity: 0;
  margin-right: -1100px;
  transition: 800ms;
}

